import {ReactCompareSlider, ReactCompareSliderImage} from "react-compare-slider";
import {useMemo, useState} from "react";

const Pagination = ({currentPage, handleSetPage, images, path}) => {
    const [positionY, setPositionY] =useState("label__position-1");
    const [positionX, setPositionX] =useState("label__positionX-1");
    const [style, setStyle] =useState("label__style-1");
    const [size, setSize] =useState("fs-1");

    const dots = () => {
        return Array.from({length: images}).map((_, i) => (
            <div className="carousel__dot-cont" onClick={() => handleSetPage(i)} key={i}>
                <div className={`${currentPage === i ? "dot-active" : ""} carousel__dot`}/>
            </div>
        ));
    };
    const galleryImages = useMemo(() => {
        return Array.from({length: images}).map((_, i) => {
            const before = `${path}/images/gallery/before${i + 1}.jpg`;
            const beforePoster = `${path}/images/gallery/before_poster${i + 1}.jpg`;
            const altBefore = `Before image ${i + 1}`;
            const after = `${path}/images/gallery/after${i + 1}.jpg`;
            const afterPoster = `${path}/images/gallery/after_poster${i + 1}.jpg`;
            const altAfter = `After image ${i + 1}`;
            return {before, beforePoster, altBefore, after, afterPoster, altAfter};
        });
    }, [images, path]);

    const handlePositionYChange = (newPositionY) => {
        setPositionY(newPositionY);
    };
    const handlePositionXChange = (newPositionX) => {
        setPositionX(newPositionX);
    };

    const handleStyleChange = (newStyle) => {
        setStyle(newStyle);
    };
    const handleSizeChange = (newSize) => {
        setSize(newSize);
    };

    return (
        <>
            <div className="buttons__board">
                <div className="flex col">
                    <div className="buttons__board-pos">
                        pos Y:
                        <button className={`board__button ${positionY === "label__position-1" ? "green_b" : ""}`}
                                onClick={() => handlePositionYChange("label__position-1")}>1</button>
                        <button className={`board__button ${positionY === "label__position-2" ? "green_b" : ""}`}
                                onClick={() => handlePositionYChange("label__position-2")}>2</button>
                        <button className={`board__button ${positionY === "label__position-3" ? "green_b" : ""}`}
                                onClick={() => handlePositionYChange("label__position-3")}>3</button>
                    </div>
                    <div className="buttons__board-pos mt-16">
                        pos X:
                        <button className={`board__button ${positionX === "label__positionX-1" ? "green_b" : ""}`}
                                onClick={() => handlePositionXChange("label__positionX-1")}>1</button>
                        <button className={`board__button ${positionX === "label__positionX-2" ? "green_b" : ""}`}
                                onClick={() => handlePositionXChange("label__positionX-2")}>2</button>
                        <button className={`board__button ${positionX === "label__positionX-3" ? "green_b" : ""}`}
                                onClick={() => handlePositionXChange("label__positionX-3")}>3</button>
                        <button className={`board__button ${positionX === "label__positionX-4" ? "green_b" : ""}`}
                                onClick={() => handlePositionXChange("label__positionX-4")}>4</button>
                    </div>
                </div>
                <div className="flex col">
                    <div className="buttons__board-style">
                        style:
                        <button className={`board__button ${style === "label__style-1" ? "green_b" : ""}`}
                                onClick={() => handleStyleChange("label__style-1")}>1</button>
                        <button className={`board__button ${style === "label__style-2" ? "green_b" : ""}`}
                                onClick={() => handleStyleChange("label__style-2")}>2</button>
                        <button className={`board__button ${style === "label__style-3" ? "green_b" : ""}`}
                                onClick={() => handleStyleChange("label__style-3")}>3</button>
                        <button className={`board__button ${style === "label__style-4" ? "green_b" : ""}`}
                                onClick={() => handleStyleChange("label__style-4")}>4</button>
                    </div>
                    <div className="buttons__board-style mt-16">
                        size:
                        <button className={`board__button ${size === "fs-1" ? "green_b" : ""}`}
                                onClick={() => handleSizeChange("fs-1")}>1</button>
                        <button className={`board__button ${size === "fs-2" ? "green_b" : ""}`}
                                onClick={() => handleSizeChange("fs-2")}>2</button>
                        <button className={`board__button ${size === "fs-3" ? "green_b" : ""}`}
                                onClick={() => handleSizeChange("fs-3")}>3</button>
                    </div>
                </div>
            </div>
            <div className="carousel relative">
                <div className="carousel__list relative">
                    <div className="carousel__item relative">
                        {galleryImages.map((item, i) => i === currentPage
                            // &&
                            ?
                            <ReactCompareSlider
                                key={i}
                                className="gallery__card slide"
                                boundsPadding={0}
                                itemOne={
                                    <>
                                        <span className={`${positionY} ${positionX} ${style} ${size} label__before`}>Before</span>
                                        <ReactCompareSliderImage loading="lazy" alt="before" src={item.before}/>
                                    </>
                                }
                                itemTwo={
                                    <>
                                        <span className={`${positionY} ${positionX} ${style} ${size} label__after`}>After</span>
                                        <ReactCompareSliderImage loading="lazy" alt="after" src={item.after}/>
                                    </>
                                }
                                keyboardIncrement="5%"
                                position={50}
                                changePositionOnHover={true}
                                handle={<></>}
                                style={{width: "100%"}}
                            />
                            : null
                        )}
                    </div>
                </div>
                <div className="carousel__dots flex absolute w-100">
                    {dots()}
                </div>
            </div>
        </>
    );
};
export default Pagination